export default {
	// name
	name: {
		// name 
		rules: [
			//  name 
			{
				required: true,
				errorMessage: '',
			}
		],
		validateTrigger: 'submit'
	},
	categoryId: {
		rules: [
			{
				required: true,
				errorMessage: ''
			}
		],
		validateTrigger: 'submit'
	},
	detail: {
		rules: [
			{
				maxLength: 50,
				errorMessage: ' 50'
			}
		]
	},
	price: {
		rules: [
			{
				required: true,
				errorMessage: ''
			},
			{
				format: 'number',
				errorMessage: ''
			},
			{
				validateFunction:function(rule,value,data,callback){
					console.log(value)
					if (value < 1) {
							callback(' 1')
				  }
				return true
			  }
			}
		],
		validateTrigger: 'submit'
	},
	unit: {
		rules: [
			{
				required: true,
				errorMessage: '',
			}
		],
		validateTrigger: 'submit'
	},
	specification: {
		rules: [
			{
				required: true,
				errorMessage: '',
			}
		],
		validateTrigger: 'submit'
	},
	address: {
		rules: [
			{
				required: true,
				errorMessage: '',
			}
		],
		validateTrigger: 'submit'
	},
	imgCount: {
		rules: [
			{
				validateFunction:function(rule,value,data,callback){
					if (value < 1) {
							callback('')
				  }
				return true
			  }
			}
		],
		validateTrigger: 'submit'
	}
}
